import React from 'react'
import { Link } from 'react-router-dom'
import ArrowKeysReact from 'arrow-keys-react'
import WheelReact from 'wheel-react'





class Navbar extends React.Component {


    constructor(props){
        super(props)
        this.state = {
            hover: false,
            hoverIcon: ''
        }
        this.toggleOn = this.toggleOn.bind(this)
        this.toggleOff = this.toggleOff.bind(this)
        this.toggleOnIcon = this.toggleOnIcon.bind(this)
        this.toggleOffIcon = this.toggleOffIcon.bind(this)
    }


        toggleOn() {
            this.setState({
                hover: true
            })
            console.log(this.state.hover)

        }

        toggleOff(event) {
               this.setState({
                   hover: false
               })
        }

        toggleOnIcon(event) {
            this.setState({
                hoverIcon: event.target.className
            })
            console.log('target', event.target.className)

        }

        toggleOffIcon(event) {
            this.setState({
                hoverIcon: ''
            })
            console.log('toggleoff')
        }


    render(){

        const projectPathnames = () => {
            return (this.props.location.pathname === '/projects' || this.props.location.pathname === '/projectone' || this.props.location.pathname === '/projecttwo' || this.props.location.pathname === '/projectthree' || this.props.location.pathname === '/projectfour' )
        }

        ArrowKeysReact.config({
            up: () => {
              if(this.props.location.pathname === '/') {
                this.props.history.push('/contact')
                } else if(this.props.location.pathname === '/skills') {
                    this.props.history.push('/')
                } else if(this.props.location.pathname === '/projects') {
                    this.props.history.push('/skills')
                }  else if(this.props.location.pathname === '/projectone') {
                    this.props.history.push('/projects')
                } else if(this.props.location.pathname === '/projecttwo') {
                    this.props.history.push('/projectone')
                } else if(this.props.location.pathname === '/projectthree') {
                    this.props.history.push('/projecttwo')
                } else if(this.props.location.pathname === '/contact') {
                    this.props.history.push('/projectthree')
                }
            },
            down: () => {
              if(this.props.location.pathname === '/') {
                this.props.history.push('/skills')
                } else if(this.props.location.pathname === '/skills') {
                    this.props.history.push('/projects')
                } else if(this.props.location.pathname === '/projects') {
                    this.props.history.push('/projectone')
                } else if(this.props.location.pathname === '/projectone') {
                    this.props.history.push('/projecttwo')
                } else if(this.props.location.pathname === '/projecttwo') {
                    this.props.history.push('/projectthree')
                } else if(this.props.location.pathname === '/projectthree') {
                    this.props.history.push('/contact')
                } else if(this.props.location.pathname === '/contact') {
                    this.props.history.push('/')
                }
            }
          });

          WheelReact.config({
            up: () => {
                if(this.props.location.pathname === '/') {
                    this.props.history.push('/contact')
                    } else if(this.props.location.pathname === '/skills') {
                        this.props.history.push('/')
                    } else if(this.props.location.pathname === '/projects') {
                        this.props.history.push('/skills')
                    } else if(this.props.location.pathname === '/projectone') {
                        this.props.history.push('/projects')
                    } else if(this.props.location.pathname === '/projecttwo') {
                        this.props.history.push('/projectone')
                    } else if(this.props.location.pathname === '/projectthree') {
                        this.props.history.push('/projecttwo')
                    } else if(this.props.location.pathname === '/contact') {
                        this.props.history.push('/projectthree')
                    }
            },
            down: () => {
                if(this.props.location.pathname === '/') {
                    this.props.history.push('/skills')
                    } else if(this.props.location.pathname === '/skills') {
                        this.props.history.push('/projects')
                    } else if(this.props.location.pathname === '/projects') {
                        this.props.history.push('/projectone')
                    } else if(this.props.location.pathname === '/projectone') {
                        this.props.history.push('/projecttwo')
                    } else if(this.props.location.pathname === '/projecttwo') {
                        this.props.history.push('/projectthree')
                    } else if(this.props.location.pathname === '/projectthree') {
                        this.props.history.push('/contact')
                    } else if(this.props.location.pathname === '/contact') {
                        this.props.history.push('/')
                    }
            }
          });




        return(
            <div id="navbar" alt="navbar" onMouseEnter={this.toggleOn} onMouseLeave={this.toggleOff} style={ this.state.hover ? { width: '180px' } : { width : '50px'} }>
                <div id="smallNav">
                    <ul>
                            <li>
                            <Link to="/" onMouseEnter={this.toggleOnIcon}>
                                {this.props.location.pathname === '/'  || this.state.hoverIcon === 'about' ?
                                <img src="/images/aboutselect.png" alt="about selected" id="aboutIcon" />
                                : <img src="/images/about.png" alt="about" id="aboutIcon" />
                                }
                            </Link>
                            </li>
                        <li>
                            <Link to="/skills" onMouseEnter={this.toggleOnIcon}>
                                {this.props.location.pathname === '/skills'  || this.state.hoverIcon === 'skills' ?
                                <img src="/images/skillsselect.png" alt="my skills selected" id="skillsIcon" />
                                : <img src="/images/skills.png" alt="my skills" id="skillsIcon" />
                                }
                            </Link>
                        </li>
                        <li >
                            <Link to="/projects" onMouseEnter={this.toggleOnIcon}>
                                {this.props.location.pathname === '/projects' || this.state.hoverIcon === 'projects' ? 
                                <img src="/images/portfolioselect.png" alt="my portfolio selected" id="projectsIcon" />
                                : <img src="/images/portfolio.png" alt="my portfolio" id="projectsIcon" />
                                }
                            </Link>
                                <ul id= "projectsUl" style={ projectPathnames() ? { height: '60px', marginBottom: '20px' } : { height : '0px', marginBottom: '0vh '} }>
                                    <Link to ="/projectone">
                                        <li style={ this.props.location.pathname === '/projectone' || this.state.hoverIcon === 'projectone' ? {color: "white"} : {color: "black"}}>1</li>
                                    </Link>
                                    <Link to ="/projecttwo">
                                        <li style={ this.props.location.pathname === '/projecttwo' || this.state.hoverIcon === 'projecttwo' ? {color: "white"} : {color: "black"}}>2</li>
                                    </Link>
                                    <Link to ="/projectthree">
                                        <li style={ this.props.location.pathname === '/projectthree' || this.state.hoverIcon === 'projectthree' ? {color: "white"} : {color: "black"}}>3</li>
                                    </Link>
                                </ul>
                        </li>
                        <li>
                            <Link to="/contact" onMouseEnter={this.toggleOnIcon}>
                                {this.props.location.pathname === '/contact' || this.state.hoverIcon === 'contact' ?
                                <img src="/images/contactselect.png" alt="contact me selected" id="contactIcon" />
                                : <img src="/images/contact.png" alt="contact me" id="contactIcon" />
                                }
                            </Link>
                        </li>
                    </ul>
                    <div id="arrowBounce">
                    {this.props.location.pathname === '/' ? 
                    <div class="downArrow bounce">
                        <img width="40" height="40" alt="" src="data:image/svg+xml;base64," />
                    </div>
                    : <p></p>
                            }
        </div>
                </div>
                    <div id="bigNav">
                    {this.state.hover?
                    <ul>
                        <li onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon} >
                            <Link to="/">
                                <p className="about">About Me</p>
                            </Link>
                        </li>
                        <li onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon} >
                            <Link to="/skills">
                                <p className="skills">Skills</p>
                            </Link>
                        </li>
                        <li id="projectsNav" onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon}>
                            <Link to="/projects">
                                <p className="projects">My Projects</p>
                            </Link>
                            <ul id= "projectsUlBig" style={ projectPathnames() ? { height: '80px', marginTop: '30px', marginBottom: '.1vh', marginLeft: '-40px' } : { height : '0px', marginBottom: '0vh '} }>
                                    <Link to ="/projectone">
                                        <li className="projectone" li onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon} style={{marginBottom: '4px', color: 'white'}}>Wirehead</li>
                                    </Link>
                                    <Link to ="/projecttwo">
                                    <li className="projecttwo" li onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon} style={{marginBottom: '4px', color: 'white'}}>Maskot</li>
                                    </Link>
                                    <Link to ="/projectthree">
                                    <li className="projectthree" li onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon} style={{marginBottom: '2px', color: 'white'}}>From Beyond</li>
                                    </Link>
                                </ul>
                            
                        </li>
                        <li onMouseEnter={this.toggleOnIcon} onMouseOut={this.toggleOffIcon}>
                            <Link to="/contact">
                                <p className="contact" style={ projectPathnames() ? {marginTop: '-30px'} : {marginTop: '-30px'}}>Contact Me</p>
                            </Link>
                        </li>
                    </ul>
                    : <p></p>}
                </div>
            </div>
        )
    }

}

export default Navbar